/* feature section css start */
.feature-section {
  padding: 30px 0;
  background-color: $base-color-two; }
.feature-item {
  @include d-flex;
  align-items: center;
  &__icon {
    width: 40px; }
  &__content {
    width: calc(100% - 40px);
    padding-left: 20px; } }
/* feature section css end */

/* category section css start */
.category-slider {
  .slick-list {
    margin: -30px -10px; }
  .single-slide {
    padding: 30px 10px; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 35px;
    height: 35px;
    margin-top: -17px;
    background-color: #fff;
    @include border-radius(50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    box-shadow: 0 0 5px rgba(#000, 0.2);
    border: 1px solid $border-color;
    &.prev {
      left: -20px;
      @include media(575px) {
        left: -5px; } }
    &.next {
      right: -20px;
      @include media(575px) {
        right: -5px; } } } }

.category-card {
  padding: rem(30px) rem(20px);
  background-color: #fff;
  border: 1px solid $border-color;
  @extend %bs-8;
  box-shadow: 0 5px 25px rgba(#000, 0.1);
  background-color: rgba($base-color, 0.05);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 35px rgba(#000, 0.15);
    border-color: $base-color;
    @include transform(translateY(-3px)); }
  &__icon {
    text-align: center;
    img {
      max-height: 60px;
      display: inline-block; } }
  &__content {
    text-align: center;
    margin-top: rem(25px); } }
/* category section css end */

/* featured course section css start */
.course-card {
  padding: rem(10px);
  @extend %bs-8;
  background-color: #fff;
  box-shadow: 0 3px 15px rgba($base-color-two, 0.15);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 25px rgba($base-color-two, 0.15);
    @include transform(translateY(-3px)); }
  &__thumb {
    @extend %bs-5;
    overflow: hidden;
    position: relative;
    height: 215px;
    .course-type {
      position: absolute;
      top: 15px;
      left: 15px;
      width: 35px;
      height: 35px;
      background-color: rgba(#fff, 0.45);
      @include border-radius(50%);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-size: rem(20px); }
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  &__content {
    padding: 20px 10px 10px 10px; }
  .course-tag {
    background-color: rgba($base-color, 0.15);
    padding: 2px 10px;
    @include border-radius(3px); }
  .course-mentor {
    @include d-flex;
    align-items: center;
    .left {
      width: 60%; }
    .right {
      width: 40%;
      text-align: right; } }
  .course-price {
    font-family: $heading-font;
    font-weight: 700;
    font-size: rem(22px);
    line-height: 1; }
  .course-footer {
    padding-top: rem(10px);
    margin-top: rem(20px);
    border-top: 1px solid $border-color;
    @include d-flex;
    align-items: center;
    .left {
      width: 50%; }
    .right {
      width: 50%;
      text-align: right; } }
  &.list-view {
    @include d-flex;
    border: 1px solid lighten($border-color, 10%);
    .course-card__thumb {
      width: 30%;
      height: auto;
      @include media(767px) {
        width: 100%; } }
    .course-card__content {
      width: calc((100% - 30%) - 200px);
      @include media(767px) {
        width: 100%; } }
    .course-footer {
      width: 200px;
      margin-top: 0;
      padding-top: 0;
      border-top: none;
      border-left: 1px solid lighten($border-color, 10%);
      text-align: right;
      padding-left: 20px;
      padding-right: 10px;
      @include media(767px) {
        width: 100%;
        padding-left: 10px;
        padding-bottom: 10px;
        border-left: none;
        padding-top: rem(10px);
        border-top: 1px solid lighten($border-color, 10%);
        margin-top: rem(15px); }
      .course-price {
        @include media(767px) {
          font-size: rem(28px); } } }
    .course-mentor {
      .right {
        text-align: left; } } } }

.teacher {
  @include d-flex;
  align-items: center;
  .thumb {
    width: 35px;
    height: 35px;
    @include border-radius(50%);
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  .name {
    width: calc(100% - 35px);
    padding-left: 15px;
    font-size: rem(14px); } }
.course-card.list-view {
  & + & {
    margin-top: 15px; } }
.ratings {
  i {
    color: #FEC42D; } }
.course-slider {
  .slick-list {
    margin: -10px; }
  .single-slide {
    padding: 10px; }
  .slick-arrow {
    position: absolute;
    top: 30%;
    width: 40px;
    height: 40px;
    background-color: #fff;
    @include border-radius(50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    box-shadow: 0 0 5px rgba(#000, 0.2);
    border: 1px solid $border-color;
    &.prev {
      left: -15px;
      @include media(575px) {
        left: 10px; } }
    &.next {
      right: -15px;
      @include media(575px) {
        right: 10px; } } } }
/* featured course section css end */

/* overview section css start */
.overview-area {
  margin-top: -60px; }
.overview-wrapper {
  padding: rem(30px) rem(30px);
  @extend %bs-8;
  background-color: #fff;
  box-shadow: 0 5px 25px rgba(#000, 0.15); }
.overview-item {
  @include d-flex;
  align-items: center;
  @include media(575px) {
    justify-content: center; }
  &__icon {
    width: 55px;
    position: relative;
    z-index: 1;
    &::after {
      position: absolute;
      content: '';
      top: -5px;
      left: -5px;
      width: 45px;
      height: 45px;
      background-color: $base-color;
      @include border-radius(50%);
      opacity: 0.15;
      z-index: -1; } }
  &__content {
    width: calc(100% - 55px);
    padding-left: 20px;
    @include media(575px) {
      width: 100%;
      padding-left: 0;
      margin-top: 10px;
      text-align: center; }
    .counter-amount {
      font-size: rem(28px); } } }
/* overview section css end */

/* mentor section css start */
.mentor-card {
  padding: rem(30px) rem(20px);
  background-color: #fff;
  @extend %bs-8;
  box-shadow: 0 5px 25px rgba(#000, 0.1);
  border: 1px solid $border-color;
  text-align: center;
  @extend %transition;
  &:hover {
    border-color: $base-color;
    box-shadow: 0 5px 35px rgba(#000, 0.15);
    @include transform(translateY(-3px)); }
  &__thumb {
    width: 170px;
    height: 170px;
    display: inline-block;
    @include border-radius(50%);
    overflow: hidden;
    img {
      width: inherit;
      height: inherit;
      @include object-fit; } }
  &__content {
    margin-top: 25px; } }
/* mentor section css end */

/* testimonial section css start */
.testimonial-card {
  padding: 0 rem(20px) rem(30px) rem(20px);
  @extend %bs-8;
  border: 1px solid rgba(#fff, 0.35);
  text-align: center;
  margin-top: 40px;
  &:hover {
    border-color: $base-color; }
  .thumb {
    width: 85px;
    height: 85px;
    display: inline-block;
    @include border-radius(50%);
    overflow: hidden;
    border: 4px solid rgba(#fff, 0.25);
    margin-top: -40px;
    img {
      width: inherit;
      height: inherit;
      @include object-fit; } } }
.testimonial-slider {
  .slick-list {
    margin: 0 -15px; }
  .single-slide {
    margin: 0 15px; }
  .slick-dots {
    @include d-flex;
    justify-content: center;
    margin-top: 20px;
    li {
      margin: 0 5px;
      &.slick-active {
        button {
          @extend %base-color;
          width: 25px; } }
      button {
        font-size: 0;
        width: 15px;
        height: 6px;
        background-color: rgba(#fff, 0.75);
        @include border-radius(999px);
        @extend %transition; } } } }
/* testimonial section css end */

/* blog section css start */
.blog-card {
  padding: rem(15px);
  background-color: #fff;
  @extend %bs-8;
  box-shadow: 0 0 15px rgba(#000, 0.12);
  border: 1px solid $border-color;
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 35px rgba(#000, 0.1);
    @include transform(translateY(-5px));
    border-color: $base-color; }
  &__thumb {
    height: 255px;
    @extend %bs-5;
    overflow: hidden;
    img {
      width: 100%;
      height: inherit;
      @include object-fit; } }
  &__content {
    padding: rem(20px) rem(10px) rem(10px) rem(10px); } }
.post-meta {
  @include d-flex;
  margin: rem(-3px) rem(-10px);
  li {
    padding: rem(3px) rem(10px);
    a {
      display: flex;
      align-items: center;
      font-size: rem(14px);
      color: $para-color;
      i {
        font-size: rem(18px);
        margin-right: 5px; } } } }
/* blog section css end */


/* blog-details-setcion css start */
.blog-details-area {
  .blog-details-thumb {
    margin-bottom: 30px; } }
.blog-details-content {
  .blog-details-title {
    font-size: rem(32px); }
  p {
    margin-top: 20px; }
  blockquote {
    margin-top: 20px;
    margin-bottom: 25px;
    padding: 30px;
    background-color: #F5F7FA;
    p {
      margin-top: 0;
      font-size: 18px;
      font-style: italic; } }
  .inner-title {
    margin-top: 20px; }
  .row {
    .inner-title {
      margin-top: 0; } }
  .cmn-list {
    @include d-flex;
    margin-right: -15px;
    li {
      width: 50%;
      padding-right: 15px;
      @media (max-width: 575px) {
        width: 100%; } }
    li+li {
      margin-top: 10px; } }
  .inner-thumb {
    @media (max-width: 991px) {
      margin-bottom: 20px; } } }
.blog-details-footer {
  margin-top: 40px;
  @include d-flex;
  justify-content: space-between;
  .share-caption {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 5px; }
  .share-post-links {
    @include d-flex;
    align-items: center;
    margin: -5px -7px;
    li {
      margin: 5px 7px;
      a {
        padding: 6px 15px;
        color: #ffffff;
        font-size: 14px;
        text-transform: uppercase;
        font-weight: 700;
        @include border-radius(5px);
        &.twitter {
          background-color: #55acee; }
        &.facebook {
          background-color: #3b5998; }
        i {
          margin-right: 7px; } } } } }
.reply-btn {
  padding: 5px 20px;
  background-color: $base-color;
  color: #000;
  @include border-radius(3px);
  font-size: 14px;
  float: right;
  margin-top: -30px;
  &:hover {
    color: #ffffff; } }
.comments-area {
  margin-top: 50px;
  .title {
    text-transform: capitalize;
    margin-bottom: 30px; } }
.comments-list {
  .single-comment {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    .thumb {
      @include flexWidth(80px);
      height: 80px;
      @include border-radius(50%);
      overflow: hidden; }
    .content {
      @include flexWidth(calc(100% - 80px));
      padding-left: 20px;
      @media (max-width: 480px) {
        @include flexWidth(100%);
        padding-left: 0;
        margin-top: 15px; }
      .name {
        text-transform: capitalize; }
      .date {
        font-size: 14px; }
      p {
        margin-top: 5px; } } } }

.comment-form-area {
  margin-top: 50px;
  .title {
    margin-bottom: 30px; }
  .comment-form {
    .form-group {
      margin-bottom: 30px; }
    .submit-btn {
      text-transform: none; } } }
/* blog-details-setcion css end */
