/* course video section css start */
#video-wrapper {
  width: 100%;
  height: 470px; }
#play-list {
  padding: 15px;
  background-color: #fff;
  @extend %bs-5;
  box-shadow: 0 0 10px rgba(#000, 0.1);
  border: 1px solid $border-color;
  max-height: 470px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: darkgrey #e7e7e7;
  &::-webkit-scrollbar {
    width: 7px; }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
  &::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    @include border-radius(999px); }
  li {
    padding: 10px 0;
    border-bottom: 1px solid lighten($border-color, 10%);
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none; } }
  .list-btn {
    background-color: transparent;
    @include d-flex;
    width: 100%; }
  .thumb {
    width: 80px;
    height: 60px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  .content {
    width: calc(100% - 80px);
    padding-left: 20px;
    text-align: left; } }

.single-annoucement {
  padding: 20px 0;
  border-bottom: 1px solid $border-color;
  &:first-child {
    padding-top: 0; }
  &:last-child {
    padding-bottom: none;
    border-bottom: none; }
  .creator {
    @include d-flex;
    align-items: center;
    .thumb {
      width: 80px;
      height: 80px;
      @include border-radius(50%);
      overflow-y: hidden; }
    .content {
      width: calc(100% - 80px);
      padding-left: 20px; } } }
/* course video section css end */
