/* footer section css start */
.footer {
  background-color: $base-color-two;
  &__top {
    padding-top: 100px;
    padding-bottom: 50px; }
  &__bottom {
    padding: 20px 0;
    position: relative;
    z-index: 1;
    &::before {
      @include positionTwo;
      background-color: darken($base-color-two, 5%);
      z-index: -1; } } }
.footer-logo {
  img {
    max-height: 80px; } }
.footer-widget {
  &__title {
    color: #fff;
    margin-bottom: rem(30px);
    @include media(1199px) {
      margin-bottom: rem(20px); } } }
.short-link-list {
  li {
    padding: 10px 0;
    border-bottom: 1px solid rgba(#fff, 0.15);
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none; }
    a {
      color: #fff;
      &:hover {
        @extend %text--base; }
      &::before {
        content: '- '; } } } }
.footer-contact-list {
  li + li {
    margin-top: 10px; }
  li {
    display: flex;
    align-items: center;
    color: #fff;
    i {
      width: 50px;
      @extend %text--base;
      font-size: rem(32px); }
    p {
      width: calc(100% - 50px);
      padding-left: 15px; }
    a {
      color: #fff; } } }
/* footer section css end */
