/* course details section css start */
.course-details-video {
  box-shadow: 0 0 5px rgba(#000, 0.15);
  position: relative;
  @extend %bs-8;
  overflow: hidden;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.55; }
  .video-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform(translate(-50%, -50%));
    width: 100px;
    height: 70px;
    background-color: $danger;
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 42px;
    @extend %bs-5;
    @include media(480px) {
      width: 70px;
      height: 50px;
      font-size: rem(36px); } } }
// .course-details-box
//   padding: 30px
//   background-color: #f3f6f9
//   border: 1px solid $border-color
//   @extend %bs-8

.course-video-list {
  li + li {
    margin-top: 10px; }
  li {
    a {
      display: flex;
      align-items: center;
      color: $para-color;
      font-size: rem(14px);
      .content {
        width: calc(100% - 30px);
        padding-right: 10px; }
      .video-status {
        width: 30px;
        text-align: right; } } } }
.instructor-card {
  padding: rem(30px);
  background-color: #f3f6f9;
  border: 1px solid $border-color;
  @extend %bs-8;
  @include d-flex;
  @include media(480px) {
    padding: rem(20px); }
  .thumb {
    width: 200px;
    @extend %bs-5;
    overflow: hidden;
    @include media(575px) {
      width: 100%; }
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  .content {
    width: calc(100% - 200px);
    padding-left: 20px;
    @include media(575px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; } } }
.instructor-info-list {
  li + li {
    margin-top: 6px; }
  li {
    display: flex;
    align-items: center;
    font-size: rem(14px);
    i {
      font-size: rem(20px);
      margin-right: 5px;
      @extend %text--base; } } }
/* course details section css end */


/* course-details-sidebar css start */
.course-details-sidebar {
  position: sticky;
  top: 100px;
  @include media(991px) {
    position: static; } }
.course-details-widget {
  padding: 30px;
  background-color: #fff;
  @extend %bs-8;
  border: 1px solid $border-color;
  box-shadow: 0 5px 35px rgba(#000, 0.15); }
.course-widget-feature {
  li + li {
    margin-top: 5px; }
  li {
    font-size: rem(14px);
    i {
      width: 25px; } } }
.course-details-review {
  padding: rem(30px);
  background-color: #fff;
  border: 1px solid $border-color;
  @extend %bs-8;
  box-shadow: 0 5px 15px rgba(#000, 0.05);
  .rating-area {
    .rating {
      font-family: $heading-font;
      font-size: rem(62px);
      font-weight: 700;
      line-height: 1; }
    .content {
      text-align: right; } } }
.single-review {
  @include d-flex;
  align-items: center;
  justify-content: space-between;
  .star {
    width: 30px; }
  .progress {
    width: calc(100% - 80px);
    height: 10px;
    .progress-bar {
      @extend %base-color; } }
  .percentage {
    width: 30px;
    text-align: right; } }

/* course-details-sidebar css end */
