/* hero section css start */
.hero {
  padding-top: rem(250px);
  padding-bottom: rem(200px);
  position: relative;
  z-index: 1;
  @include media(1199px) {
    padding-bottom: rem(150px); }
  @include media(575px) {
    padding-bottom: rem(120px); }
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.65;
    z-index: -1; }
  &__title {
    font-size: rem(62px);
    @include media(1399px) {
      font-size: rem(52px); }
    @include media(991px) {
      font-size: rem(42px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__description {
    width: 70%;
    margin: 0 auto;
    @include media(991px) {
      width: 90%; }
    @include media(767px) {
      width: 100%; } } }
.hero-search-form {
  background-color: #fff;
  padding: rem(10px);
  @extend %bs-8;
  @include d-flex;
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: -10px;
    left: -10px;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: #fff;
    opacity: 0.15;
    @extend %bs-8;
    z-index: -2; }
  .form--control {
    border: none;
    flex-grow: 1;
    flex-shrink: 1;
    width: auto;
    &:focus {
      box-shadow: none; } }
  &__btn {
    padding: rem(10px) rem(30px);
    @extend %base-color;
    @extend %bs-5;
    color: #fff; } }
/* hero section css end */


/* inner hero section start */
.inner-hero {
  padding-top: rem(210px);
  padding-bottom: rem(70px);
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.65;
    z-index: -1; } }
/* inner hero section end */
