/* dashboard section css start */
.widget {
  $icon: rem(65px);
  padding: rem(20px) rem(20px);
  border: 1px solid $border-color;
  box-shadow: 0 3px 10px rgba($base-color, 0.1);
  &__icon {
    width: $icon;
    height: $icon;
    font-size: calc(#{$icon} / 2); }
  &__content {
    width: calc(100% - #{$icon});
    padding-left: rem(25px); }
  &__number {
    font-size: rem(28px); } }

.short-course-card {
  padding: rem(10px);
  @extend %bs-5;
  background-color: #fff;
  box-shadow: 0 3px 10px rgba(#000, 0.1);
  .thumb {
    @extend %bs-5;
    overflow: hidden;
    height: 200px;
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  .content {
    padding: 20px 10px 10px 10px; }
  .dropdown {
    .dropdown-toggle {
      background-color: #fff;
      box-shadow: 0 0 8px rgba(#000, 0.08);
      @include border-radius(5px);
      &::after {
        display: none; } } } }
.dropdown-menu {
  border: 1px solid lighten($border-color, 10%);
  box-shadow: 0 5px 25px rgba(#000, 0.1); }
/* dashboard section css end */
